* {
	box-sizing: border-box;
}

body {
	padding: 0;
	margin: 0;
	border: 0;
	font-family: 'Museo Sans', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

ul.accordion-list {
	position: relative;
	display: block;
	width: 100%;
	height: auto;
	// padding: 20px;
	margin: 0;
	list-style: none;
	background-color: #f9f9fa;

	li {
		position: relative;
		display: block;
		width: 100%;
		height: auto;
		background-color: #fff;
		padding: 20px;
		margin: 0 auto 0 auto;
		border: 1px solid #eee;
		border-radius: 3px;
		cursor: pointer;

		&.active {
			background-color:#e9e9e9;
			h3 {
				&:after {
					transform: rotate(45deg);
				}
			}
		}

		h3 {
			font-weight: 700;
			position: relative;
			display: block;
			width: 100%;
			height: auto;
			padding: 0 0 0 0;
			margin: 0;
			font-size: 15px;
			letter-spacing: 0.01em;
			cursor: pointer;

			&:after {
				content: '\f278';
				font-family: 'material-design-iconic-font';
				position: absolute;
				right: 0;
				top: 0;
				color: #fcc110;
				transition: all 0.3s ease-in-out;
				font-size: 18px;
			}
		}

		div.answer {
			position: relative;
			display: block;
			width: 100%;
			height: auto;
			margin: 0;
			padding: 1rem 0;
			cursor: pointer;

			p {
				position: relative;
				display: block;
				font-weight: 300;
				padding: 10px 0 0 0;
				cursor: pointer;
				line-height: 150%;
				margin: 0 0 15px 0;
				font-size: 14px;
			}
		}
	}
}
