html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

a {
  text-decoration: none;
  color: black;
}

h1,
h2,
h3,
h4,
h5,
h6,
h7,
p,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  padding: 0 1rem;
}

.m-0 {
  margin: 0;
}

.mx-0 {
  margin-left: 0;
  margin-right: 0;
}

.my-0 {
  margin-top: 0;
  margin-bottom: 0;
}

.p-0 {
  padding: 0;
}

.px-0 {
  padding-left: 0;
  padding-right: 0;
}

.py-0 {
  padding-top: 0;
  padding-bottom: 0;
}

.m0p0 {
  padding: 0;
  margin: 0;
}

.b-0 {
  border: 0;
}

.b-none {
  border: none;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.block {
  display: block;
}

.grid {
  display: grid;
}

.flex {
  display: flex;
}

.flex-row {
  flex-direction: row;
}

.flex-col {
  flex-direction: column;
}

.justify-start {
  justify-content: flex-start;
}

.justify-center {
  justify-content: center;
}

.justify-end {
  justify-content: flex-end;
}

.justify-between {
  justify-content: space-between;
}

.justify-around {
  justify-content: space-around;
}

.items-start {
  align-items: start;
}

.items-center {
  align-items: center;
}

.items-end {
  align-items: end;
}

.gap {
  gap: 0.2rem;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
  height: 100%;
}

.position {
  position: fixed;
}

.sticky {
  position: sticky;
  z-index: 1;
}

/* SCROLL */
/* width */
::-webkit-scrollbar {
  width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.airport-section-content::-webkit-scrollbar:horizontal {
  display: none;
}

body {
  background-color: white;
  z-index: -999;
}

.navbar {
  width: 100%;
  z-index: 1;
  background-color: white;
  padding: 1rem 0;
  border-bottom: 1px solid #e5ebf0;
  position: sticky;
  top: 0;
  /* Adding transform to the X */
  /* Styling the three lines
  to make it an X */
  /* Adding a transition delay to the
  3 items in the navigation menu */
}
.navbar .menu-btn {
  margin-left: 1rem;
}
.navbar .menu-btn .btn-line {
  width: 22px;
  height: 2.8px;
  margin: 0 0 2px 0;
  background: #262626;
  cursor: pointer;
  transition: all 0.5s ease-out;
}
.navbar .btn-line:nth-child(3) {
  margin: 0;
}
.navbar .menu-btn.close {
  transform: rotate(180deg);
}
.navbar .menu-btn.close .btn-line:nth-child(1) {
  transform: rotate(45deg) translate(2px, 5px);
}
.navbar .menu-btn.close .btn-line:nth-child(2) {
  opacity: 0;
}
.navbar .menu-btn.close .btn-line:nth-child(3) {
  transform: rotate(-45deg) translate(2px, -5px);
}
.navbar .nav-item:nth-child(1) {
  transition-delay: 0.1s;
}
.navbar .nav-item:nth-child(2) {
  transition-delay: 0.2s;
}
.navbar .nav-item:nth-child(3) {
  transition-delay: 0.3s;
}
.navbar .w-logo {
  width: 2rem;
}
.navbar .map-svg {
  margin-right: 1rem;
}

.menu-modal {
  position: absolute;
  top: 2.5em;
  background-color: white;
  padding: 2em;
  border-radius: 10px;
  font-size: 1.2em;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  transition: left 200ms ease-in-out;
  left: -1000px;
  width: 90%;
  height: 30rem;
  z-index: 1;
}
.menu-modal.show {
  visibility: visible;
}

.close ~ .menu-modal {
  left: 0;
  right: 0;
  margin: auto;
  z-index: 1;
}

.title-wrap {
  padding: 0 1rem;
}
.title-wrap .title {
  text-align: center;
  font-size: 0.95rem;
  font-weight: 700;
  color: white;
}

.search {
  padding: 1rem 0;
  font-size: 0.9rem;
  font-weight: 300;
}
.search border .text-search {
  color: white;
}
.search .input-search {
  height: 2rem;
  margin: 0.5rem 0;
}
.search input {
  height: 100%;
  border-radius: 5px;
  border: none;
}
.search .btn-search {
  text-align: center;
  border: none;
  width: 40%;
  border-radius: 5px;
  height: 1.5rem;
}

.continents-wrap {
  font-size: 0.9rem;
  padding-top: 1rem;
}
.continents-wrap .continents {
  border-radius: 8px;
  box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.5);
}

.continent-wrap {
  width: 100%;
  background-color: #f7fbff;
  margin: 1px solid black;
  font-size: 0.95rem;
  border-top: 0.5px solid #b8c2cc81;
  gap: 1rem;
}
.continent-wrap .continent {
  text-align: center;
  font-weight: 700;
}

.social-footer {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.airport-main {
  background-color: #e6e6e640;
}

.main-content-category {
  margin: 1rem 1rem 0 1rem;
}
.main-content-category .main-category {
  background-color: white;
  padding: 0.3rem 0.5rem;
  font-size: 0.9rem;
  width: 100%;
  text-align: center;
}
.main-content-category .portada-cat,
.main-content-category .pais-cat {
  border-right: 1px solid #e5ebf0;
}
.main-content-category .category-selected {
  border-top: 2px solid #0d6efd;
}

.main-header {
  border-radius: 5px;
  margin: 0 1rem 1rem 1rem;
  padding: 0.5rem 0.5rem 0.5rem 0.5rem;
  background-color: white;
}
.main-header .main-section {
  height: 8rem;
}
.main-header .main-section-img {
  display: none;
  padding: 0.5rem;
}
.main-header .main-section-img img {
  border: 2px solid #e5ebf0;
  width: 7rem;
  height: 7rem;
  border-radius: 50%;
  box-sizing: border-box;
  filter: drop-shadow(2px 4px 20px rgba(101, 101, 101, 0.15));
}
.main-header .main-section-country {
  display: none;
  text-align: center;
}
.main-header .main-section-country .city-airport {
  font-size: 0.7rem;
  font-weight: 300;
}
.main-header .main-section-country .country-airport {
  font-size: 0.9rem;
  font-weight: 300;
}
.main-header .main-section-country .country-flag {
  padding-top: 0.5rem;
  width: 5rem;
}
.main-header .main-section-climate {
  display: none;
}
.main-header .main-section-climate .hour {
  font-size: 1.7rem;
  text-align: center;
  font-stretch: expanded;
}
.main-header .section-visible {
  display: flex;
}
.main-header .airport-name {
  grid-column-start: 1;
  grid-column-end: span 4;
  justify-self: center;
  text-align: center;
  font-size: 1rem;
  padding: 0.5rem 0.5rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 5px;
  width: 100%;
}
.main-header .arrow-back {
  top: 10px;
  right: 10px;
  width: 18px;
  height: 18px;
  background-color: #0d6efd;
  border-radius: 5px;
  color: white;
}

.info {
  font-size: 0.9rem;
  margin: 1rem;
  padding: 0.5rem;
  background-color: #e5ebf0;
}

.airport-sections {
  font-size: 0.95rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  position: fixed;
  bottom: 0;
  background: rgba(255, 255, 255, 0.35);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(7px);
  -webkit-backdrop-filter: blur(7px);
  border: 1px solid rgba(255, 255, 255, 0.18);
}
.airport-sections .arrow-section-container {
  padding: 0.5rem 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  opacity: 0.5;
  background: linear-gradient(270deg, #00000085, #fff0);
}
.airport-sections .arrow-section-container .arrow-rotate {
  transform: rotate(180deg);
}
.airport-sections .airport-section-content {
  display: grid;
  padding: 0 1rem;
  grid-template-columns: repeat(8, 1fr);
  grid-column-gap: 2rem;
  overflow-x: scroll;
}
.airport-sections .airport-section-content .airport-section {
  display: flex;
  width: 3rem;
  height: 3rem;
}
.airport-sections .airport-section-content .airport-section .circle-sections {
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
}
.airport-sections .airport-section-content .airport-section p {
  font-size: 0.8rem;
}

.airport-main {
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  border: 0;
  font-family: "Museo Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul.accordion-list {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  margin: 0;
  list-style: none;
  background-color: #f9f9fa;
}
ul.accordion-list li {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  background-color: #fff;
  padding: 20px;
  margin: 0 auto 0 auto;
  border: 1px solid #eee;
  border-radius: 3px;
  cursor: pointer;
}
ul.accordion-list li.active {
  background-color: #e9e9e9;
}
ul.accordion-list li.active h3:after {
  transform: rotate(45deg);
}
ul.accordion-list li h3 {
  font-weight: 700;
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  padding: 0 0 0 0;
  margin: 0;
  font-size: 15px;
  letter-spacing: 0.01em;
  cursor: pointer;
}
ul.accordion-list li h3:after {
  content: "\f278";
  font-family: "material-design-iconic-font";
  position: absolute;
  right: 0;
  top: 0;
  color: #fcc110;
  transition: all 0.3s ease-in-out;
  font-size: 18px;
}
ul.accordion-list li div.answer {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  margin: 0;
  padding: 1rem 0;
  cursor: pointer;
}
ul.accordion-list li div.answer p {
  position: relative;
  display: block;
  font-weight: 300;
  padding: 10px 0 0 0;
  cursor: pointer;
  line-height: 150%;
  margin: 0 0 15px 0;
  font-size: 14px;
}